<template>
  <v-container>



    <div v-show="lovelovelist.length == 0 ? true : false">
      ËøòÊ≤°ÊúâÂñúÊ¨¢ÁöÑÂõæÁâáÔºåÁÇπÂáªÂõæÁâá‰∏ãÊñπÁ∫¢Ëâ≤Áà±ÂøÉ
      <v-icon color="red">favorite_outline</v-icon>ÂñúÊ¨¢Êî∂ËóèÂõæÁâá„ÄÇ
    </div>

    <div>
      <div class="img-box" v-for="item in lovelovelist" :key="item.id">

        <div @click="canvasco">
          <v-img @click="updatesdetail(item)" :src="item.src" :aspect-ratio="279.984 / 186.656" />
        </div>

        <span class="res-span">{{ item.res }}</span>

        <span class="down-span">
          <a :href="item.src">‰øùÂ≠ò</a>
        </span>

        <span @click="favorite_item(item)" class="like-span">
          <v-btn v-show="favorState(item) == false" icon color="red">
            <v-icon>favorite_border</v-icon>
          </v-btn>
          <v-btn v-show="favorState(item) == true" icon color="red">
            <v-icon>favorite</v-icon>
          </v-btn>
        </span>
      </div>

      <div class="image-background" v-show="isdtpageshow">
        <!-- ÂõæÂÉèÁîªÂ∏É -->
        <canvas></canvas>

        <!-- ÁîªÂ∏É ÂÖ≥Èó≠ÊåâÈíÆ-->
        <span class="close-span">
          <v-icon color="white" @click="closedetailpage">close</v-icon>
        </span>

        <!-- ÂõæÂÉèÂàÜËæ®Áéá -->
        <div class="image-resolution">
          <span>{{ imgselect.res }}</span>
        </div>

        <!-- ÂõæÂÉèÁªÜËäÇËèúÂçïÂäüËÉΩ -->
        <div class="image-tools-bar">
          <v-btn @click="backone" title="‰∏ä‰∏ÄÂº†" class="tool" text>
            <v-icon>arrow_back</v-icon>
          </v-btn>

          <v-btn @click="favorite_item(imgselect)" v-show="favorState(imgselect) == false" title="ÂñúÊ¨¢" class="tool" text>
            <v-icon color="red">favorite_border</v-icon>
          </v-btn>

          <v-btn @click="favorite_item(imgselect)" v-show="favorState(imgselect) == true" title="ÂñúÊ¨¢" class="tool" text>
            <v-icon color="red">favorite</v-icon>
          </v-btn>

          <v-btn :href="imgselect.src" title="‰∏ãËΩΩ" class="tool" a text>
            <v-icon color="green">file_download</v-icon>
          </v-btn>

          <v-btn @click="toggleFullScreen" v-show="isFull" title="ÂÖ®Â±è" class="tool" text>
            <v-icon color="blue">fullscreen</v-icon>
          </v-btn>

          <v-btn @click="toggleFullScreen" v-show="!isFull" title="ÁªìÊùüÂÖ®Â±è" class="tool" text>
            <v-icon color="blue">fullscreen_exit</v-icon>
          </v-btn>

          <v-btn @click="nextone" title="‰∏ã‰∏ÄÂº†" class="tool" text>
            <v-icon>arrow_forward</v-icon>
          </v-btn>
        </div>
      </div>
    </div>

    <div class="text-center pagination-bar">
      <v-container>
        <div class="vue_pagination">
          <v-pagination v-model="page" :length="length" @input="$vuetify.goTo(0, 'duration'), connectStore()"
            prev-icon="arrow_left" next-icon="arrow_right"></v-pagination>
          <hr />

          <v-btn>ÂΩìÂâçÂÖ± {{ this.total }} Âº† / {{ this.length }} È°µ Page = {{ this.page }}</v-btn>
        </div>
      </v-container>
    </div>

  </v-container>
</template>


<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";


export default {
  name: "MyFavorites",

  data: () => ({

    //basement
    lovelovelist: [],

    // statement
    isdtpageshow: false,
    isFull: true,
    imgindex: 0,
    imgselect: {},
    thesize: { dx: 0, dy: 0, width: 0, height: 0 },

    // page,something
    length: null,
    total: null,
    page: 1

  }),

  computed: {

    ...mapState("settings", { Defaultstate: "Defaultstate" }),

    ...mapState("favorites", { lovelistids: "lovelistids" }),

  },

  methods: {

    ...mapActions('favorites', {
      favorite_item: "FAVORITE_IT",
    }),

    ...mapMutations("favorites", {
      CONNECT: "CONNECT_IDB",
    }),

    ...mapMutations("generals", {

      testapi: "HELLO"

    }),

    resolveAfter2Seconds(x) {
      return new Promise(resolve => {
        setTimeout(() => {

          resolve(x);
          console.log(x);

        }, 2000);

      });
    },

    async fff1() {
      // Á¨¨‰∏ÄÊ≠•
      let x = await this.resolveAfter2Seconds(10);
      console.log("HELLO");
    },

    // Fontend Context
    favorState(item) {

      if (this.lovelistids.includes(item.id)) {
        return true;
      } else {
        return false;
      }

    },

    /**
     * ÁîªÂ∏ÉcanvasËÆæÁΩÆ
     * ÁªòÂà∂,‰º†ÂÖ•thesizeÔºåË¶ÅÈáçÊñ∞ÁªòÂà∂ÁöÑÂèÇÊï∞
     */
    canvasco() {
      const canvas = document.querySelector("canvas");
      const ctx = canvas.getContext("2d");
      const thing = new Image();

      // ÊääÈÄâÊã©ÁöÑÂØπË±°ÂõæÂÉèË∑ØÂæÑ‰º†ÂÖ• Êñ∞Âª∫ÁöÑÂØπË±°thing
      thing.src = this.imgselect.src;

      // ÊåÇËΩΩÂõæÂÉèÂØπË±° thing
      thing.onload = function () {
        const size = resize();
        // console.log("thing", size.dx, size.dy, size.width, size.height)
        ctx.drawImage(thing, size.dx, size.dy, size.width, size.height);
      };

      window.onresize = function () {
        // console.log("ÈáçÊñ∞ÂÆö‰πâÂ§ßÂ∞è")
        const size = resize();
        ctx.drawImage(thing, size.dx, size.dy, size.width, size.height);
      };

      function resize() {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        // console.log("window", canvas.width, canvas.height)
        let dx = 0;
        let dy = 0;
        let width = thing.naturalWidth;
        let height = thing.naturalHeight;

        // console.log("thesize", width, height)

        if (canvas.width / canvas.height <= width / height) {
          height = (height / width) * canvas.width;
          width = canvas.width;
          dy = (canvas.height - height) / 2;
        } else {
          width = (width / height) * canvas.height;
          height = canvas.height;
          dx = (canvas.width - width) / 2;
        }

        // console.log("resize", dx, dy, height, width)

        return {
          dx: dx,
          dy: dy,
          width: width,
          height: height
        };
      }
    },

    // ÂÖ≥Èó≠ÊåâÈíÆ
    closedetailpage() {

      this.exitfullscreen();
      // this.opendetailpage();
      this.isdtpageshow = false

      // Ë∞ÉÊï¥ html overflow Â±ûÊÄß
      const htmlStyle = document.getElementsByTagName("html")[0].style;
      htmlStyle.overflowY = "";
    },

    // ÂàáÊç¢ÂÖ®Â±èÂäüËÉΩ
    toggleFullScreen() {
      if (!document.fullscreenElement) {
        // ÂÖ®Â±èÂäüËÉΩüëá,ËøõË°å‰∏Ä‰∏™Á™óÂè£ÂÖ®Â±è, ËèúÂçïÊ†èÁä∂ÊÄÅÂèòÊç¢
        document.documentElement.requestFullscreen();

        this.isFull = false;
      } else {
        // ÈÄÄÂá∫ÂÖ®Â±èÂäüËÉΩüëáÔºåËøõË°å‰∏Ä‰∏™Á™óÂè£ÈÄÄÂá∫ÂÖ®Â±è
        if (document.exitFullscreen) {
          document.exitFullscreen();

          this.isFull = true;
        }
      }
    },

    // ‰∏ä‰∏ÄÂº†
    backone() {

      if (0 < this.imgindex) {
        this.imgindex--;
        this.imgselect = this.lovelovelist[this.imgindex];
        this.canvasco();
        console.log(this.imgindex);
      } else if (0 == this.imgindex) {
        console.log("Â∑≤ËææÂà∞Á¨¨‰∏ÄÂº†ÔºåÊó†Ê≥ïËøõË°å‰∏Ä‰∏™Âáè");
      } else {
        console.log("ÂÖ∂‰ªñÈîôËØØÔºÅ");
      }

    },

    // ‰∏ã‰∏ÄÂº†
    nextone() {
      if (this.imgindex < Number(this.lovelovelist.length) - 1) {
        this.imgindex++;
        this.imgselect = this.lovelovelist[this.imgindex];
        this.canvasco();

      } else if (this.imgindex == Number(this.lovelovelist.length) - 1) {
        console.log("Â∑≤ËææÂà∞ÊúÄÂêé‰∏ÄÂº†ÔºåÊó†Ê≥ïËøõË°å‰∏Ä‰∏™ + üòµ");
      } else {
        console.log("ÂÖ∂‰ªñÈîôËØØÔºÅ");
      }
    },

    updatesdetail(item) {
      const htmlStyle = document.getElementsByTagName("html")[0].style;
      htmlStyle.overflowY = "hidden";

      this.imgselect = item;

      this.imgindex = this.lovelovelist.indexOf(item);
      // console.log(this.imgindex);
      // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
      this.opendetailpage();
    },

    opendetailpage() {
      this.isdtpageshow = !this.isdtpageshow;
    },

    // ÈÄÄÂá∫ÂÖ®Â±è
    exitfullscreen() {
      if (document.fullscreenElement) {
        console.log("ÂÖ®Â±èÁä∂ÊÄÅÔºåÈÄÄÂá∫ÂÖ®Â±è");
        document.exitFullscreen();

        this.isFull = true;
      }
    },

    // Special + ËøûÊé•‰ªìÂ∫ì + Êï∞ÊçÆËé∑Âèñ
    connectStore() {

      this.lovelovelist = [];

      const request = indexedDB.open("ttbz_wallpaper");

      request.onerror = event => { console.log("ÂèëÁîüÈîôËØØÔºÅ", event); };

      request.onsuccess = event => {
        const db = event.target.result;
        this.db = db;

        const objectStore = db.transaction("favorites").objectStore("favorites");

        objectStore.count().onsuccess = event => {
          this.total = event.target.result;

          if (this.total / this.Defaultstate.size <= 1) {
            this.length = 1;
          } else {

            this.length = parseInt(this.total / this.Defaultstate.size) + 1;
          }
        };

        let index = null;
        objectStore.openCursor().onsuccess = event => {
          const cursor = event.target.result;

          if (cursor) {
            if (index === this.Defaultstate.size - 1) {
              this.lovelovelist.push(cursor.value);

              // console.log("ËØªÂèñÊï∞ÊçÆÊàêÂäüÔºö", this.lovelovelist);
              // console.log("ÊÄªÊù°ÁõÆ", this.total);

              return null;
            } else if (index === null && this.page !== 1) {
              console.log(
                "ËØªÂèñË∑≥ËøáÔºö",
                (this.page - 1) * this.Defaultstate.size
              );
              index = 0;
              cursor.advance((this.page - 1) * this.Defaultstate.size);
            } else {
              index++;
              this.lovelovelist.push(cursor.value);
              cursor.continue();
            }
          } else {
            // console.log("ËØªÂèñÊï∞ÊçÆÊàêÂäüÔºö", this.lovelovelist);
            // console.log("ÊÄªÊù°ÁõÆ", this.total);
          }



        };
      }

    },


  },

  watch: {

    lovelistids: {
      deep: true,
      handler(value) {
        localStorage.setItem("lovelistids", JSON.stringify(value));
      }
    }

  },


  created() {
    this.fff1();

    // WORKED
    this.CONNECT();
    this.connectStore();
  },
  beforemounted() {

  },
  mounted() {

  },



};
</script>